*, :after, :before {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  --dark-color: #000;
}

body {
  perspective: 1000px;
  background-color: var(--dark-color);
  justify-content: center;
  align-items: flex-end;
  min-height: 100vh;
  padding: 50px 0;
  display: flex;
  overflow: hidden;
}

.night {
  filter: blur(.1vmin);
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
}

.flowers {
  position: relative;
  transform: scale(.9);
}

.flower {
  transform-origin: bottom;
  z-index: 10;
  --fl-speed: .8s;
  position: absolute;
  bottom: 10vmin;
}

.flower--1 {
  animation: 4s linear infinite moving-flower-1;
}

.flower--1 .flower__line {
  height: 70vmin;
  animation-delay: .3s;
}

.flower--1 .flower__line__leaf--1 {
  animation: blooming-leaf-right var(--fl-speed) 1.6s backwards;
}

.flower--1 .flower__line__leaf--2 {
